export default {
    props: {
        ticketList: {
            type: Array,
            default() {
                return []
            }
        },
        showTicketList: {
            type: Boolean,
            default: false
        },
        showPrice: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        choseTicket (tiketObj) {
            this.$emit('choseTicket', tiketObj)
        },
        priceText (tiketObj) {
            let temp = JSON.parse(tiketObj.sellType)
            if (temp.oldPriceRule) {
                return `价格：${tiketObj.price/100}元`
            } 
            return `套票价格：${tiketObj.discountPrice/100}元`
        }
    },
}
